.activity-projects{display:flex;flex-flow:row wrap}.activity-projects .project{margin:20px;width:350px;height:160px;font-size:13px;white-space:pre-wrap;overflow:hidden}.activity-projects .project .ant-card-body{height:100%;padding:10px;display:flex;flex-direction:column;justify-content:space-between}.activity-projects .project-name{font-size:1.1rem}.activity-projects .project-description{opacity:.8;height:65px;overflow:hidden;text-overflow:ellipsis;display:-webkit-box;-webkit-line-clamp:3;-webkit-box-orient:vertical}.activity-projects .project-icons{display:flex;justify-content:flex-start}.activity-projects .project-icons .caption{margin-left:5px}.activity-projects .project-icons .watchers{margin:0}.activity-projects .project-icons .stars{margin-left:15px}.activity-projects .project-icons .forks{margin-left:15px}.activity-projects .project-languages{margin-top:5px;display:flex;align-items:center}.activity-projects .project-languages .language{margin-right:10px;display:flex;align-items:center}.activity-projects .project-languages .language-color{margin-right:5px;width:12px;height:12px;border-radius:50%}
